.avatar {
  margin: 10px;
  display: flex;
}
.avatar .image-wrapper {
  display: flex;
  flex-direction: column;
}
.avatar .image-wrapper .image {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 2px solid white;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center;
}
.avatar .image-wrapper .person {
  background-image: url(../../static/media/person.6a136b2f.jpeg);
}
